.modal {
    right: 10px;
    top: 0%;
    position: absolute;
    display : none;
    background-color: #f6f6f6;
}

.modalBtn {
    background-color: #03a8f4; /* Green background */
    border: none;
    border-radius: 5px;
    color: white; /* White text */
    padding: 5px 10px; /* Some padding */
    cursor: pointer; /* Pointer/hand icon */
    font-size: 1.1em;
    text-transform: uppercase;
}

.modalBtn:hover {
    background-color: #3d8e41;
}

.modalWrapper {
    display: none;
}

.modalWrapper.showModal {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, 0.5);
    overflow-y: scroll;
}

.modalBody {
    width: 80%;
    left: 10%;
    max-width: 600px;
    border-radius: 5px;
    top: 10%;
    position: absolute;
    background-color: aliceblue;
}

.modalBody .h2 {
    margin: 0 auto;
    background-color: black;
    color: white;
    font-size: 2em;
    text-align: justify;
    padding: 5px;
    border-radius: 5px;
}

.modalMain {
    padding: 10px;
}

.close {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 30px;
    height: 30px;
    font-size: 1.2em;
    background-color: aliceblue;
    border: none;
    outline: none;
    border-radius: 3px;
}

@media screen and (min-width: 609px) {
    .modalBody {
        left: 10%;
    }
}

@media screen and (min-width: 790px){
    .modalBody {
        left: 20%;
    }
}

@media screen and (min-width: 980px) {
    .modalBody {
        left: 22%;
    }
}

